<template>
  <div class="app-container">
    <div class="filter-container">
      <el-date-picker
        v-model="date"
        type="date"
        placeholder="Please pick a date"
      />
      <el-button
        v-waves
        class="filter-item"
        type="primary"
        icon="el-icon-search"
        @click="handleFilter"
      >
        搜索
      </el-button>
    </div>
    <el-table
      v-loading="listLoading"
      :data="list"
      element-loading-text="Loading"
      border
      fit
      highlight-current-row
    >
      <el-table-column align="center" label="ID" width="95">
        <template slot-scope="scope">
          {{ scope.$index }}
        </template>
      </el-table-column>
      <el-table-column label="日期" align="center">
        <template slot-scope="scope">
          <span>{{
            scope.row.utime | timestampFilter("{y}-{m}-{d} {h}:{i}")
          }}</span>
        </template>
      </el-table-column>
      <el-table-column label="来源" align="center">
        <template slot-scope="scope">
          {{ scope.row.origin }}
        </template>
      </el-table-column>
      <el-table-column label="价格" align="center">
        <template slot-scope="scope">
          <el-tag type="success">{{ scope.row.price / 100 }}</el-tag>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      :total="total"
      :page.sync="listQuery.pageNo"
      :limit.sync="listQuery.pageSize"
      @pagination="fetchData"
    />
  </div>
</template>

<script>
import { parseTime } from "@/utils";
import { purchaseList } from "@/api/record";
import waves from "@/directive/waves"; // waves directive
import Pagination from "@/components/Pagination";

export default {
  components: { Pagination },
  directives: { waves },
  filters: {
    timestampFilter(time, fm) {
      return parseTime(time, fm);
    }
  },
  data() {
    return {
      total: 0,
      listQuery: {
        dateStr: "",
        pageNo: 1,
        pageSize: 20
      },
      date: new Date(),
      list: null,
      listLoading: true
    };
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      this.listLoading = true;
      this.listQuery.dateStr = parseTime(this.date, "{y}-{m}-{d}");
      purchaseList(this.listQuery).then(response => {
        if (response.code === 0) {
          this.list = response.data.record;
          this.total = response.data.total;
          this.listLoading = false;
        } else {
          this.$message({
            message: response.message,
            type: "warning"
          });
          this.listLoading = false;
        }
      });
    },
    handleFilter() {
      this.listQuery.pageNo = 1;
      this.fetchData();
    }
  }
};
</script>
